@use "../settings";
@use "../settings-options";
@use "border";

// Modal

// Allow modal to fill viewport
@mixin modal-fullscreen() {
  width: 100vw;
  max-width: none;
  max-height: 100vh;
  margin: 0;
  overflow-y: auto; // Scroll here to not overlap the side of `.modal-content`

  @at-root .modal.fade #{&} {
    transform: settings.$modal-transform-fullscreen;
  }

  .modal-content {
    height: min-content;
    min-height: 100vh;
    border: 0;
    @include border.border-radius(0);
  }

  @if settings-options.$enable-modal-scrollable {
    &.modal-dialog-scrollable {
      overflow-y: hidden;
    }
  }

  @if settings-options.$enable-modal-centered {
    &.modal-dialog-centered {
      align-items: stretch;
    }
  }
}
