// Embedded icons from Open Iconic.
// Released under MIT and copyright 2014 Waybury.
// https://github.com/iconic/open-iconic

@use "sass:map";
@use "sass:string";
@use "functions/assert";
@use "functions/calc-util";
@use "functions/encode-svg";
@use "functions/palette";
@use "functions/map-util";
@use "functions/rem-calc";
@use "functions/str-util";

// Prefix for :root CSS variables
$prefix: cf- !default;

// Options
// =====
// Quickly modify global styling by enabling or disabling optional features.
$enable-rounded:                true !default;      // true
$enable-shadows:                false !default;     // false
$enable-transitions:            true !default;      // true
$enable-transitions-reduced:    true !default;      // true
$enable-smooth-scroll:          true !default;      // true
$enable-grid-classes:           true !default;      // true
$enable-print-styles:           true !default;      // true
$enable-sizing:                 true !default;      // true
$enable-bp-smallest:            false !default;     // false
$enable-rfs-main:               false !default;       // true :TODO:
$enable-color-warnings:         false !default;     // false

// Palette levels for use in mix-context-colors() to determine
// standardized contextual color mapping.
// Special values:
//  level < 0 = #fff
//  level > 1000 = #000
$level-control: (
  "bg":                   500,
  "color":                -1,
  "border-color":         600,
  "hover-bg":             600,
  "hover-color":          -1,
  "hover-border-color":   700,
  "active-bg":            700,
  "active-color":         -1,
  "active-border-color":  700
) !default;

$level-context: (
  "bg":                   100,
  "color":                800,
  "border-color":         200,
  "hover-bg":             200,
  "hover-color":          900,
  "hover-border-color":   300,
  "active-bg":            300,
  "active-color":         900,
  "active-border-color":  300
) !default;

// Increase (or decrease with negative number) to alter color for the hover state of
// `.text-{color}-(palette-level}` utilities
$level-delta-hover-color:           100 !default;

// Colors
// =====
// Base Colors
$uibase:    #5e7182 !default;
$white:     #fff !default;
$gray:      #666 !default;
$black:     #000 !default;
$red:       #d42314 !default;
$green:     #0e8a16 !default;
$blue:      #0055e9 !default;
$cyan:      #1680a4 !default;
$yellow:    #f8c223 !default;

// Base UI Scale
$uibase-50:   palette.palette($uibase, 50) !default;
$uibase-100:  palette.palette($uibase, 100) !default;
$uibase-200:  palette.palette($uibase, 200) !default;
$uibase-300:  palette.palette($uibase, 300) !default;
$uibase-400:  palette.palette($uibase, 400) !default;
$uibase-500:  palette.palette($uibase, 500) !default;
$uibase-600:  palette.palette($uibase, 600) !default;
$uibase-700:  palette.palette($uibase, 700) !default;
$uibase-800:  palette.palette($uibase, 800) !default;
$uibase-900:  palette.palette($uibase, 900) !default;

// Color Variables
$primary:   $blue !default;
$secondary: $uibase !default;
$info:      $cyan !default;
$success:   $green !default;
$warning:   $yellow !default;
$danger:    $red !default;
$light:     palette.palette($uibase, 50) !default;
$dark:      palette.palette($uibase, 800) !default;

// Base Color Map - used for generating contextual variants
$base-colors: (
  "primary":   $primary,
  "secondary": $secondary,
  "info":      $info,
  "success":   $success,
  "warning":   $warning,
  "danger":    $danger,
  "light":     $light,
  "dark":      $dark
) !default;

// Palette Colors
// =====
$palette-colors: (
  "uibase":    $uibase,
  "primary":   $primary,
  "secondary": $secondary,
  "info":      $info,
  "success":   $success,
  "warning":   $warning,
  "danger":    $danger,
  "gray":      $gray
) !default;

// Color Contrast
// =====
// Minimum contrast ratio - WCAG 2.1 level AA spec
$color-contrast-min-ratio:  4.5 !default;
$color-contrast-base-light: $white !default;
$color-contrast-base-dark:  $uibase-900 !default;

// Component Color Variants and Levels
// =====
$root-colors:           map.merge($base-colors, $palette-colors) !default;
$alert-colors:          $base-colors !default;
$btn-colors:            $base-colors !default;
$btn-outline-colors:    $base-colors !default;
$list-colors:           $base-colors !default;
$table-colors:          $base-colors !default;
$utility-bg-colors:     $base-colors !default;
$utility-bgtext-colors: $base-colors !default;
$utility-border-colors: $base-colors !default;
$utility-text-colors:   $base-colors !default;

$alert-levels:          $level-context !default;
$btn-levels:            $level-control !default;
$btn-outline-levels:    $level-control !default;
$list-levels:           $level-context !default;
$table-levels:          $level-context !default;

// Component Theme Initialization
// =====
// Themes are mixed, using `$*-colors` and `$*-levels`, and merged
// inside each of their component sources.  They are included here
// for including additional color themes.
$alert-themes:          () !default;
$btn-themes:            () !default;
$btn-outline-themes:    () !default;
$list-themes:           () !default;
$table-themes:          () !default;

// Spacing
// =====
// Control the default spacing of most elements by modifying these variables.
$spacer:   1rem !default;
$spacers: (
  "0":    0,
  "0_25": ($spacer * .25),
  "0_5":  ($spacer * .5),
  "1":    $spacer,
  "1_5":  ($spacer * 1.5),
  "2":    ($spacer * 2)
) !default;

$spacers-gap:                       $spacers !default;
$spacers-padding:                   $spacers !default;
$spacers-margin:                    $spacers !default;
$spacers-margin-negative:           $spacers !default;
$spacers-margin-negative-prepend:   n !default;

// Typography
// =====
// Font, line-height, and color for body text, headings, and more.
// stylelint-disable value-keyword-case
$font-family-sans-serif:    system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-serif:         "Iowan Old Style", "Apple Garamond", Baskerville, "Times New Roman", "Droid Serif", Times, "Source Serif Pro", serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace:     SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:          var(--#{$prefix}font-family-sans-serif) !default;
$font-family-code:          var(--#{$prefix}font-family-monospace) !default;
// stylelint-enable value-keyword-case

// `$font-size-root` affects the value of `rem`, which is used for as well font sizes, paddings and margins
// `$font-size-base` affects the font size of the body text
$font-size-root:    null !default;
$font-size-base:    1rem !default;
$line-height-base:  1.5 !default;

// Only used when one of Responsive Typography methods is enabled.
$responsive-font-size-minimum-size:           1rem !default;
$responsive-font-size-fluid-breakpoint:       rem-calc.bp-to-em(1200px) !default;
$responsive-font-size-fluid-factor:           10 !default;
$responsive-font-size-two-dimensional:        false !default;
$responsive-font-size-generate-class:         false !default;
$responsive-font-size-fluid-two-dimensional:  false !default;

// Font size utility classes
$font-sizes: (
  "xsmall":   ($font-size-base * .75),
  "small":    ($font-size-base * .875),
  "base":     $font-size-base,
  "large":    ($font-size-base * 1.125),
  "xlarge":   ($font-size-base * 1.25),
  "2xlarge":  ($font-size-base * 1.5),
  "3xlarge":  ($font-size-base * 1.75),
  "4xlarge":  ($font-size-base * 2),
  "5xlarge":  ($font-size-base * 2.5)
) !default;

// Line height utility classes
$line-heights: (
  "small":    1.25,
  "base":     $line-height-base,
  "large":    1.75
) !default;

$font-weight-lighter:   lighter !default;
$font-weight-light:     300 !default;
$font-weight-normal:    400 !default;
$font-weight-medium:    500 !default;
$font-weight-semibold:  600 !default;
$font-weight-bold:      700 !default;
$font-weight-black:     900 !default;
$font-weight-bolder:    bolder !default;
$font-weight-base:      $font-weight-normal !default;

// Font weight utility classes
$font-weights: (
  "light":    $font-weight-light,
  "normal":   $font-weight-normal,
  "medium":   $font-weight-medium,
  "semibold": $font-weight-semibold,
  "bold":     $font-weight-bold,
  "black":    $font-weight-black,
  "lighter":  $font-weight-lighter,
  "bolder":   $font-weight-bolder
) !default;

// Borders
// =====
// Border dimension in pixels to get proper visual layout
$border-width:      1px !default;
$border-color:      $uibase-200 !default;
$border-radius:     .375rem !default;

// Body
// =====
$body-bg:           $white !default;
$body-color:        $uibase-900 !default;
$body-text-align:   null !default;

// Links
// =====
$link-color:                    $primary !default;
$link-decoration:               underline !default;
$link-hover-color:              palette.palette($primary, 700) !default;
$link-hover-decoration:         null !default;

$link-stretch-pseudo-element:   after !default;
$link-stretch-z-index:          1 !default;

// Headings
// =====
$h1-font-size:  ($font-size-base * 2.5) !default;
$h2-font-size:  ($font-size-base * 2) !default;
$h3-font-size:  ($font-size-base * 1.75) !default;
$h4-font-size:  ($font-size-base * 1.5) !default;
$h5-font-size:  ($font-size-base * 1.25) !default;
$h6-font-size:  ($font-size-base * 1) !default;

$headings-margin-bottom:  ($spacer * .5) !default;
$headings-font-family:    null !default;
$headings-font-style:     null !default;
$headings-font-weight:    600 !default;
$headings-line-height:    1.25 !default;
$headings-color:          inherit !default;

// Paragraph
// =====
$paragraph-spacer-y:    1rem !default;

// Special text treatment
// =====
$small-font-size:       .875em !default;
$sub-sup-font-size:     .75em !default;
$initialism-font-size:  .875em !default;
$dt-font-weight:        $font-weight-bold !default;

$text-muted:        $uibase-500 !default;

// Lead
// =====
$lead-font-size:    ($font-size-base * 1.25) !default;
$lead-font-weight:  600 !default;
$lead-line-height:  $line-height-base !default;

// Rules
// =====
$hr-margin-y:       1rem !default;
$hr-color:          inherit !default;
$hr-border-width:   $border-width !default;
$hr-border-color:   null !default;
$hr-opacity:        .25 !default;

$vr-border-width:   $hr-border-width !default;
$vr-border-color:   $hr-border-color !default;
$vr-opacity:        $hr-opacity !default;

// Mark/highlight
// =====
$mark-padding:      .125em .25em !default;
$mark-color:        $body-color !default;
$mark-bg:           #ff0 !default;

// Code
// =====
$code-font-size:      .875em !default;
$code-padding-x:      null !default;
$code-padding-y:      null !default;
$code-color:          palette.palette($red, 550) !default;
$code-bg:             null !default;
$code-border-radius:  null !default;

$kbd-font-size:       .875em !default;
$kbd-padding-x:       .375rem !default;
$kbd-padding-y:       .125rem !default;
$kbd-bg:              $uibase-900 !default;
$kbd-color:           $uibase-50 !default;
$kbd-border-radius:   .1875rem !default;

$pre-color:           null !default;

// Blockquote
// =====
$blockquote-margin:             0 0 $spacer !default;
$blockquote-font-size:          ($font-size-base * 1.25) !default;
$blockquote-color:              $body-color !default;
$blockquote-footer-margin:      -$spacer 0 $spacer !default;
$blockquote-footer-font-size:   ($font-size-base * .875) !default;
$blockquote-footer-color:       $uibase-500 !default;
$blockquote-footer-content:     "\2014\00a0" !default; // em dash, nbsp

// Component base colors
// =====
$component-bg:                  $white !default;
$component-border-color:        $border-color !default;

$component-hover-bg:            $uibase-50 !default;

$component-action-color:        $uibase-700 !default;
$component-action-hover-color:  $uibase-800 !default;

$component-active-bg:           $primary !default;
$component-active-color:        $white !default; //color-util.color-if-contrast($white, $primary) !default; :TODO:
$component-active-border-color: palette.palette($primary, 600) !default;

$component-disabled-color:      $uibase-300 !default;
$component-disabled-bg:         transparent !default;

$component-overlay-border-color:    rgba($uibase-900, .4) !default;
$component-section-bg:              rgba($uibase-900, .0875) !default;
$component-section-border-color:    rgba($uibase-900, .3) !default;

// Component sizes
// =====
// Used for button, button groups, pagination, form-control, and input-group
// 'md' is considered to be the default size, so no classes for this size designator are generated, as in *no* `.btn-md`.
$component-sizes: (
  "xsmall": (
    "padding-y":     null,
    "padding-x":     null,
    "font-size":     ($font-size-base * .75),
    "line-height":   null,
    "border-radius": .25rem
  ),
  "small": (
    "padding-y":     null,
    "padding-x":     null,
    "font-size":     ($font-size-base * .875),
    "line-height":   null,
    "border-radius": .25rem
  ),
  "large": (
    "padding-y":     null,
    "padding-x":     null,
    "font-size":     ($font-size-base * 1.125),
    "line-height":   null,
    "border-radius": .375rem
  ),
  "xlarge": (
    "padding-y":     null,
    "padding-x":     null,
    "font-size":     ($font-size-base * 1.25),
    "line-height":   null,
    "border-radius": .5rem
  )
) !default;

// Per component/utility sizing
$btn-sizes:             $component-sizes !default;
$input-sizes:           $component-sizes !default;
$pagination-sizes:      $component-sizes !default;

// Shadows
// =====
// Control the `box-shadow` depth.
$shadows: (
  "0":  none,
  "d1": 0 .125rem .1875rem rgba($black, .15),
  "d2": 0 .125rem .25rem rgba($black, .15),
  "d3": 0 .125rem .5rem rgba($black, .15),
  "d4": 0 .125rem .75rem rgba($black, .15),
  "d5": 0 .125rem 1rem rgba($black, .15),
  "i1": inset 0 .125rem .1875rem rgba($black, .15),
  "i2": inset 0 .125rem .25rem rgba($black, .15),
  "i3": inset 0 .125rem .5rem rgba($black, .15),
  "i4": inset 0 .125rem .75rem rgba($black, .15),
  "i5": inset 0 .125rem 1rem rgba($black, .15)
) !default;

// Transitions
// =====
// Widget animations
$transition-base:           all .15s ease-in-out !default;
$transition-fade:           opacity .15s linear !default;
$transition-lazy-fade:      opacity .3s linear !default;
$transition-collapse-y:     height .3s ease !default;
$transition-collapse-x:     width .3s ease !default;

// Component transitions
$btn-transition:            color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;
$input-transition:          background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;
$offcanvas-transition:      transform .3s linear !default;
$modal-transition:          transform .15s linear !default;
$navbar-toggle-transition:  box-shadow .15s ease-in-out !default;
$nav-link-transition:       color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out !default;
$progress-bar-transition:   width .3s ease !default;
$switch-transition:         all .15s ease-in-out !default;

// Tables
// =====
$table-margin-bottom:           $spacer !default;
$table-cell-padding:            .5rem !default;
$table-condensed-cell-padding:  .25rem !default;
$table-caption-color:           $uibase-500 !default;

$table-cell-vertical-align:     top !default;
$table-head-vertical-align:     bottom !default;
$table-head-horizontal-align:   left !default;

$table-bg:                      null !default;
$table-bg-active:               $component-hover-bg !default;
$table-bg-active-hover:         $uibase-200 !default;

$table-th-font-weight:          null !default;

$table-border-width:            $border-width !default;
$table-border-color:            $component-border-color !default;
$table-head-border-width:       2 * $table-border-width !default;
$table-head-border-color:       null !default;
$table-body-border-width:       2 * $table-border-width !default;
$table-body-border-color:       null !default;
$table-foot-border-width:       2 * $table-border-width !default;
$table-foot-border-color:       null !default;

$table-striped-selector:        odd !default;
$table-striped-cols-selector:   even !default;
$table-striped-box-shadow:      inset 0 0 0 999rem rgba($uibase-900, .075) !default;
$table-striped-alt-box-shadow:  inset 0 0 0 999rem rgba($white, .125) !default;

$table-hover-box-shadow:        inset 0 0 0 999rem rgba($uibase-900, .125) !default;
$table-hover-alt-box-shadow:    inset 0 0 0 999rem rgba($white, .2) !default;

// Buttons
// =====
$btn-padding-y:             .25em !default;
$btn-padding-x:             .75em !default;
$btn-font-family:           null !default;
$btn-font-size:             $font-size-base !default;
$btn-font-weight:           $font-weight-normal !default;
$btn-line-height:           1.5 !default;
$btn-white-space:           null !default;
$btn-border-width:          $border-width !default;
$btn-border-radius:         $border-radius !default;
$btn-box-shadow:            inset 0 .125em .25em rgba($white, .25), 0 .125em .125em rgba($black, .075) !default;
$btn-active-box-shadow:     inset 0 .25em .3125em rgba($black, .125) !default;

$btn-focus-box-shadow-size:  0 0 0 .1875rem !default;
$btn-focus-box-shadow-alpha: .35 !default;
$btn-focus-box-shadow:       $btn-focus-box-shadow-size rgba($component-active-bg, $btn-focus-box-shadow-alpha) !default;

$btn-outline-bg:            transparent !default;

$btn-disabled-opacity:      .6 !default;

$btn-block-spacing-y:       .25rem !default;
$btn-icon-multiplier:       .5 !default;

$btn-default-bg:                    $white !default;
$btn-default-color:                 $uibase-500 !default; //color-util.color-if-contrast($uibase-500, $btn-default-bg) !default; :TODO:
$btn-default-border-color:          $uibase-300 !default;
$btn-default-focus-box-shadow-color: $component-active-bg !default;
$btn-default-hover-bg:              $uibase-50 !default;
$btn-default-hover-color:           $uibase-600 !default; //color-util.color-if-contrast($uibase-600, $btn-default-hover-bg) !default; :TODO:
$btn-default-hover-border-color:    $uibase-400 !default;
$btn-default-active-bg:             $uibase-200 !default;
$btn-default-active-color:          $btn-default-hover-color !default; //color-util.color-if-contrast($btn-default-hover-color, $btn-default-active-bg) !default; :TODO:
$btn-default-active-border-color:   $uibase-400 !default;
$btn-default-disabled-bg:           $btn-default-bg !default;
$btn-default-disabled-color:        $btn-default-color !default;
$btn-default-disabled-border-color: $btn-default-border-color !default;

$btn-link-color:                    $link-color !default;
$btn-link-hover-color:              $link-hover-color !default;
$btn-link-disabled-color:           $btn-link-color !default;

// Forms
// =====
// Padding and line-height should add up to buttons for inputs and selects
$label-margin-bottom:           .5rem !default;

$legend-margin-bottom:          .5rem !default;
$legend-font-size:              1.5rem !default;
$legend-font-weight:            null !default;

$input-font-family:             null !default;
$input-font-size:               $btn-font-size !default;
$input-font-weight:             $font-weight-normal !default;
$input-line-height:             $btn-line-height !default;
$input-padding-y:               $btn-padding-y !default;
$input-padding-x:               $btn-padding-x !default;

$input-bg:                      $white !default;
$input-color:                   $uibase-700 !default;
$input-border-color:            $uibase-200 !default;
$input-border-width:            $border-width !default;
$input-border-radius:           $border-radius !default;
$input-box-shadow:              map.get($shadows, "i1") !default;

$input-focus-bg:                $input-bg !default;
$input-focus-color:             $input-color !default;
$input-focus-border-color:      palette.palette($primary, 300) !default;
$input-focus-box-shadow-size:   0 0 0 .1875rem !default;
$input-focus-box-shadow-alpha:  .35 !default;
$input-focus-box-shadow:        $input-focus-box-shadow-size rgba($component-active-bg, $input-focus-box-shadow-alpha) !default;

$input-disabled-bg:             $uibase-50 !default;
$input-disabled-color:          $component-disabled-color !default;
$input-disabled-border-color:   null !default;
$input-disabled-opacity:        1 !default;

$input-placeholder-color:       #999 !default;

$input-static-color:            $body-color !default;

$input-checkradio-size:         .875rem !default;

$form-label-font-weight:        $font-weight-normal !default;

$form-text-margin-top:          .25rem !default;
$form-text-color:               $text-muted !default;
$form-text-font-size:           $small-font-size !default;
$form-text-font-style:          null !default;
$form-text-font-weight:         null !default;
$form-text-line-height:         null !default;

$form-group-margin-bottom:      1rem !default;
$form-row-gutter-width:         .625rem !default;

$form-inline-breakpoint:        sm !default;
$form-inline-check-margin-x:    .25em !default;

// Form check
$form-check-gutter:                 1.25em !default;
$form-check-margin-bottom:          .25rem !default;
$form-check-label-color:            null !default;
$form-check-label-font-weight:      null !default;
$form-check-label-cursor:           null !default;
$form-check-label-disabled-opacity: .6 !default;

// Form checkradio
$form-checkradio-size:                      1em !default;
$form-checkradio-gutter:                    calc-util.add($form-checkradio-size, .375em) !default;
$form-checkradio-bg:                        $white !default;
$form-checkradio-border-width:              $border-width !default;
$form-checkradio-border-color:              $uibase-300 !default;
$form-checkradio-box-shadow:                map.get($shadows, "i1") !default;
$form-checkradio-icon-size:                 calc-util.subtract($form-checkradio-size, .375em) !default;

$form-checkradio-focus-border-color:        $input-focus-border-color !default;
$form-checkradio-focus-box-shadow:          $input-focus-box-shadow !default;

$form-checkradio-checked-bg:                $primary !default;
$form-checkradio-checked-color:             $white !default;
$form-checkradio-checked-border-color:      $primary !default;
