// Grid breakpoints
// =====
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.
// If only one breakpoint is desired, use: $grid-breakpoints: (xs: 0);
$grid-breakpoints: (
  "xs": 0,
  "sm": rem-calc.bp-to-em(576px),
  "md": rem-calc.bp-to-em(768px),
  "lg": rem-calc.bp-to-em(992px),
  "xl": rem-calc.bp-to-em(1200px)
) !default;

// stylelint-disable-next-line scss/dollar-variable-default
$grid-breakpoints: map-util.map-sort-by-values($grid-breakpoints);

@include assert.assert-ascending($grid-breakpoints, "$grid-breakpoints");
@include assert.assert-starts-at-zero($grid-breakpoints, "$grid-breakpoints");

// Containers
// =====
// Define the maximum width of `.container` for different screen sizes.
$container-max-widths: (
  "sm": rem-calc.rem(544px),
  "md": rem-calc.rem(720px),
  "lg": rem-calc.rem(960px),
  "xl": rem-calc.rem(1152px)
) !default;

@include assert.assert-ascending($container-max-widths, "$container-max-widths");

// Grid
// =====
// Set the number of columns and specify the width of the gutter.
$grid-columns:      12 !default;
$grid-gutter-width: 2rem !default;
$grid-row-columns:  6 !default;

$gutters: (
  "0":    0,
  "0_5":  .5rem,
  "1":    1rem,
  "1_5":  1.5rem,
  "2":    2rem,
  "2_5":  2.5rem
) !default;

// Container padding
// =====
$container-padding-x:   $grid-gutter-width * .5 !default;

// Component and utility breakpoints
// =====
// Each component and utility that uses breakpoints gets its own list
// of breakpoint keys based upon the master `$grid-breakpoints` map.
// This way each the CSS for each of the items can be controlled and reduced as needed.
// The items in the following lists *must* be in the keys for the `$grid-breakpoints` map.
$responsive-container-breakpoints:  map.keys($grid-breakpoints) !default;
$responsive-gutter-breakpoints:     map.keys($grid-breakpoints) !default;
$row-columns-breakpoints:           map.keys($grid-breakpoints) !default;
$card-horizontal-breakpoints:       map.keys($grid-breakpoints) !default;
$card-deck-breakpoints:             map.keys($grid-breakpoints) !default;
$card-group-breakpoints:            map.keys($grid-breakpoints) !default;
$card-columns-breakpoints:          map.keys($grid-breakpoints) !default;
$navbar-expand-breakpoints:         map.keys($grid-breakpoints) !default;
$offcanvas-breakpoints:             map.keys($grid-breakpoints) !default;
$table-scroll-breakpoints:          map.keys($grid-breakpoints) !default;
$modal-fullscreen-breakpoints:      map.keys($grid-breakpoints) !default;
$utility-border-breakpoints:        map.keys($grid-breakpoints) !default;
$utility-display-breakpoints:       map.keys($grid-breakpoints) !default;
$utility-flex-breakpoints:          map.keys($grid-breakpoints) !default;
$utility-float-breakpoints:         map.keys($grid-breakpoints) !default;
$utility-object-fit-breakpoints:    map.keys($grid-breakpoints) !default;
$utility-position-breakpoints:      map.keys($grid-breakpoints) !default;
$utility-sticky-top-breakpoints:    map.keys($grid-breakpoints) !default;
$utility-sticky-bottom-breakpoints: map.keys($grid-breakpoints) !default;
$utility-placement-breakpoints:     "xs" !default;
$utility-translate-breakpoints:     "xs" !default;
$utility-screen-reader-breakpoints: map.keys($grid-breakpoints) !default;
$utility-spacing-breakpoints:       map.keys($grid-breakpoints) !default;
